import { useState, useEffect, useMemo } from 'react'

import { useNavigate, useSearchParams } from 'react-router-dom'

import {
  PlayArrow as PlayIcon,
  Pause as PauseIcon,
  Stop as StopIcon,
  Delete as DeleteIcon,
} from '@mui/icons-material'
import {
  Box,
  Typography,
  Button,
  Paper,
  Stack,
  Alert,
  CircularProgress,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
} from '@mui/material'

import { createTrainingExecution } from '@/api/trainingExecutions'
import { getTrainingPlans } from '@/api/trainingPlans'
import NumberSpinner from '@/components/NumberSpinner'
import { useAuth } from '@/contexts/AuthContext'
import { useTimer, formatTime } from '@/hooks/useTimer'
import { routes } from '@/routes'
import type { TrainingPlan } from '@/types/database'

type TrainingState = 'idle' | 'running' | 'paused' | 'completed'

export function ExecutionsNewPage() {
  const navigate = useNavigate()
  const { user } = useAuth()
  const [searchParams] = useSearchParams()
  const initialPlanId = searchParams.get('planId')

  const [plans, setPlans] = useState<TrainingPlan[]>([])
  const [localPlanId, setLocalPlanId] = useState<number | ''>('')
  const [localUnitCount, setLocalUnitCount] = useState<number>(1)
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const timer = useTimer()

  // Derive training state from timer state
  const trainingState: TrainingState = useMemo(() => {
    if (timer.isRunning) return 'running'
    if (timer.isPaused) return 'paused'
    return 'idle'
  }, [timer.isRunning, timer.isPaused])

  // Use timer's planId when active, otherwise use local state
  const selectedPlanId = trainingState !== 'idle' ? (timer.planId ?? '') : localPlanId
  const unitCount = trainingState !== 'idle' ? (timer.unitCount ?? 1) : localUnitCount

  const selectedPlan = plans.find((p) => p.id === selectedPlanId)

  useEffect(() => {
    loadPlans()
  }, [])

  // Set initial plan from URL params only when timer is idle
  useEffect(() => {
    if (initialPlanId && plans.length > 0 && trainingState === 'idle') {
      const planId = Number(initialPlanId)
      const plan = plans.find((p) => p.id === planId)
      if (plan && !timer.planId) {
        setLocalPlanId(planId)
        setLocalUnitCount(plan.unit_amount)
      }
    }
  }, [initialPlanId, plans, trainingState, timer.planId])

  // Update local unit count when plan changes in idle state
  useEffect(() => {
    if (trainingState === 'idle' && localPlanId) {
      const plan = plans.find((p) => p.id === localPlanId)
      if (plan) {
        setLocalUnitCount(plan.unit_amount)
      }
    }
  }, [localPlanId, plans, trainingState])

  async function loadPlans() {
    try {
      setLoading(true)
      const data = await getTrainingPlans()
      setPlans(data)
    } catch (err) {
      setError('훈련 계획을 불러오는데 실패했습니다.')
      // eslint-disable-next-line no-console
      console.error(err)
    } finally {
      setLoading(false)
    }
  }

  function handleStart(planId: number, count: number) {
    if (!planId) {
      setError('훈련 계획을 선택해주세요.')
      return
    }
    setError(null)
    timer.start({ planId, unitCount: count })
  }

  function handleUnitCountChange(count: number) {
    if (trainingState === 'idle') {
      setLocalUnitCount(count)
    } else {
      timer.setUnitCount(count)
    }
  }

  function handlePause() {
    timer.pause()
  }

  function handleResume() {
    timer.resume()
  }

  async function handleComplete() {
    // console.log('handleComplete', timer)
    if (!timer.planId || !timer.startedAt) return

    try {
      setSaving(true)
      setError(null)

      const completedAt = new Date()
      await createTrainingExecution({
        user_id: user!.id,
        plan_id: timer.planId,
        started_at: timer.startedAt.toISOString(),
        completed_at: completedAt.toISOString(),
        duration_seconds: timer.elapsedSeconds,
        unit_count: timer.unitCount ?? 1,
      })

      timer.reset()
      navigate(routes.executions.$())
    } catch (err) {
      setError('훈련 기록 저장에 실패했습니다.')
      // eslint-disable-next-line no-console
      console.error(err)
    } finally {
      setSaving(false)
    }
  }

  function handleCancel() {
    timer.reset()
  }

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', py: 4 }}>
        <CircularProgress />
      </Box>
    )
  }

  if (plans.length === 0) {
    return (
      <Alert severity="warning">
        훈련 계획이 없습니다. 먼저 훈련 계획을 생성해주세요.
        <Button component="a" href={routes.plans.new.$()} size="small" sx={{ ml: 2 }}>
          계획 만들기
        </Button>
      </Alert>
    )
  }

  return (
    <Box>
      <Typography variant="h5" gutterBottom>
        훈련 수행
      </Typography>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}

      <Stack spacing={3}>
        {/* 훈련 계획 선택 */}
        <FormControl fullWidth disabled={trainingState !== 'idle'}>
          <InputLabel>훈련 계획</InputLabel>
          <Select
            value={selectedPlanId}
            label="훈련 계획"
            onChange={(e) => setLocalPlanId(e.target.value as number)}
          >
            {plans.map((plan) => (
              <MenuItem key={plan.id} value={plan.id}>
                {plan.name} ({plan.unit_amount} {plan.unit_name}/회)
              </MenuItem>
            ))}
          </Select>
        </FormControl>

        {/* 훈련량 입력 */}
        <NumberSpinner
          label={`훈련량 (${selectedPlan?.unit_name ?? '단위'})`}
          min={1}
          value={unitCount}
          onValueChange={(value) => {
            if (value) {
              handleUnitCountChange(value)
            }
          }}
        />

        {/* 타이머 표시 */}
        <Paper
          sx={(theme) => ({
            p: 4,
            textAlign: 'center',
            bgcolor:
              trainingState === 'running'
                ? theme.palette.mode === 'dark'
                  ? 'primary.dark'
                  : 'primary.light'
                : trainingState === 'paused'
                  ? theme.palette.mode === 'dark'
                    ? 'warning.dark'
                    : 'warning.light'
                  : theme.palette.mode === 'dark'
                    ? 'grey.800'
                    : 'grey.100',
          })}
        >
          <Typography
            variant="h1"
            sx={(theme) => ({
              fontFamily: 'monospace',
              fontWeight: 'bold',
              fontSize: {
                xs: '2.5rem',
                sm: '4rem',
                md: '5rem',
                lg: '6rem',
              },
              color:
                trainingState === 'running'
                  ? 'primary.contrastText'
                  : trainingState === 'paused'
                    ? 'warning.contrastText'
                    : theme.palette.mode === 'dark'
                      ? 'text.primary'
                      : 'text.primary',
            })}
          >
            {formatTime(timer.elapsedSeconds)}
          </Typography>
          {trainingState === 'paused' && (
            <Typography variant="body2" sx={{ mt: 1 }}>
              일시정지됨
            </Typography>
          )}
        </Paper>

        {/* 컨트롤 버튼 */}
        <Stack direction="row" spacing={2} justifyContent="center">
          {trainingState === 'idle' && (
            <Button
              variant="contained"
              size="large"
              startIcon={<PlayIcon />}
              onClick={() => handleStart(localPlanId as number, localUnitCount)}
              disabled={!localPlanId}
            >
              시작
            </Button>
          )}

          {trainingState === 'running' && (
            <>
              <Button
                variant="outlined"
                // size="large"
                startIcon={<PauseIcon />}
                onClick={handlePause}
              >
                일시정지
              </Button>
              <Button
                variant="contained"
                color="success"
                // size="large"
                startIcon={<StopIcon />}
                onClick={handleComplete}
                disabled={saving}
              >
                {saving ? '저장 중...' : '완료'}
              </Button>
              <Button
                variant="outlined"
                color="error"
                // size="large"
                startIcon={<DeleteIcon />}
                onClick={handleCancel}
              >
                삭제
              </Button>
            </>
          )}

          {trainingState === 'paused' && (
            <>
              <Button
                variant="contained"
                size="large"
                startIcon={<PlayIcon />}
                onClick={handleResume}
              >
                재개
              </Button>
              <Button
                variant="contained"
                color="success"
                size="large"
                startIcon={<StopIcon />}
                onClick={handleComplete}
                disabled={saving}
              >
                {saving ? '저장 중...' : '완료'}
              </Button>
              <Button variant="outlined" color="error" size="large" onClick={handleCancel}>
                취소
              </Button>
            </>
          )}
        </Stack>

        {trainingState === 'idle' && (
          <Button variant="text" onClick={() => navigate(routes.executions.$())}>
            돌아가기
          </Button>
        )}
      </Stack>
    </Box>
  )
}
